@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@600&family=Work+Sans:wght@300;500&display=swap);
/* RESET CSS */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* FONTS */
.App {
  width: 100%;
  height: 100%;
  font-family: 'Work Sans', sans-serif;
  font-weight: 500;
  color: #fff;
  background-color: rgb(210, 210, 210);
}

.App-title {
  padding: .8rem 0;
  font-family: 'Poppins', sans-serif;
  font-size: 2rem;
  text-align: center;
  color: #fff;
  background-color: #1db954;
}

.App-playlist {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  padding-bottom: 4rem;
}

@media only screen and (max-width: 1020px) {
  .App-playlist {
    align-items: center;
    flex-direction: column;
  }
}
.SearchBar {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 4rem;
  padding: 5%;
  font-family: 'Work Sans', sans-serif;
  background-image: url(/spotlist/static/media/background_photo_desktop.37be7357.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.SearchBar input {
  width: 35%;
  border: 1px solid #fff;
  border-radius: 5px;
  padding: 1rem 0;
  margin-bottom: 2rem;
  font-size: 1rem;
  font-weight: 500;
  text-align: center;
  color: #321184;
  opacity: .8;
  transition: all .3s;
}

.SearchBar input:focus {
  outline: none;
  opacity: 1;
  box-shadow: 0px 0px 5px 2px #fff
}

.SearchButton {
  width: 15%;
  border: 0px;
  border-radius: 50px;
  padding: 1rem 0;
  font-size: 1rem;
  font-weight: 500;
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: #1db954;
  transition: all .3s;
}

.SearchButton:hover {
  background-color: rgba(30, 215, 96, 0.7);
}
.SearchResults {
  width: 45%;
  height: 950px;
  border-radius: 20px;
  padding: 2rem;
  background-color: #321184;
  overflow-y: scroll;
  box-shadow: 5px 5px 10px 1px rgba(0,0,0,0.4);
}

.SearchResults-title {
  width: 100%;
  height: 3rem;
  border-bottom: 1px solid #fff;
  padding: 0 0 1rem 0;
  font-family: 'Poppins', sans-serif;
  font-size: 2rem;
  font-weight: 400;
  color: #fff;
  background: transparent;
}

::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}

@media only screen and (max-width: 1020px) {
  .SearchResults {
    width: 90%;
    margin-bottom: 2rem;
  }
}
.TrackList {
  width: 100%;
}
.Track {
  display: flex;
  align-items: center;
  padding: 0 1rem;
  border-bottom: 1px solid #fff;
}

.Track:hover {
  background-color: rgba(200, 200, 200, .2);
}

.Track-information {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-grow: 1;
  height: 72px;
  color: #fff;
}

.Track-information h3 {
  margin-bottom: .22rem;
  font-size: 1.1rem;
  font-weight: 500;
}

.Track-information p {
  font-size: .9rem;
  font-weight: 300;
}

.Track-action {
  border: 0px;
  border-radius: 5px;
  padding: .6rem;
  font-size: 1.2rem;
  color: #fff;
  background-color: transparent;
  cursor: pointer;
  transition: all .3s;
}

.Track-action.add:hover {
  color: #fff;
  background-color: #1db954;
}

.Track-action.remove:hover {
  color: #fff;
  background-color: #321184;
}


.Playlist {
  display: flex;
  flex-flow: column;
  align-items: center;
  width: 40%;
  height: 950px;
  border-radius: 20px;
  padding: 2rem;
  background-color: #1db954;
  overflow-y: scroll;
  box-shadow: 5px 5px 10px 1px rgba(0,0,0,0.4);
}

.Playlist input {
  width: 100%;
  height: 3rem;
  border: 0;
  border-bottom: 1px solid #fff;
  outline: 0;
  padding: 0 0 1rem 0;
  font-family: 'Poppins', sans-serif;
  font-size: 2rem;
  color: #fff;
  background: transparent;
}

.Playlist-save {
  width: 50%;
  margin-top: 2rem;
  border: 0px;
  border-radius: 50px;
  padding: 1rem 0;
  font-size: 1rem;
  font-weight: 600;
  text-align: center;
  color: #1db954;
  cursor: pointer;
  background-color: #fff;
  transition: all .3s;
}

.Playlist-save:hover {
  color: #fff;
  background-color: #321184;
}

::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}

@media only screen and (max-width: 1020px) {
  .Playlist {
    width: 90%;
  }
}
